
import { defineComponent } from 'vue';
import VueMarkdown from 'vue-markdown-render';

export default defineComponent({
  name: 'ModalLightbox',
  components: {
    VueMarkdown,
  },
  data() {
    return {
      lightboxVisible: false,
    };
  },
  props: {
    headingText: {
      required: true,
      type: String,
    },
    textSource: {
      required: true,
      type: String,
    },
    teaserText: {
      required: true,
      type: String,
    },
  },
  computed: {
    text() {
      return require(`../assets/markdown/${this.textSource}`).default;
    },
  },
  methods: {
    showLightbox() {
      this.lightboxVisible = true;
      document.addEventListener('click', this.handleOutsideClick);
    },
    hideLightbox() {
      this.lightboxVisible = false;
      document.removeEventListener('click', this.handleOutsideClick);
    },
    handleOutsideClick(event: { target: any; }) {
      const lightboxContent = document.querySelector('.lightbox-content--box');
      if (
        this.lightboxVisible && !lightboxContent.contains(event.target) && !event.target.classList.contains('lightbox-button--open')
      ) {
        this.hideLightbox();
        console.log('Hier');
      }
    },
  },
});
