
import { defineComponent } from 'vue';
import VueMarkdown from 'vue-markdown-render';
import ModalLightbox from './ModalLightbox.vue';

export default defineComponent({
  name: 'FullSizeText',
  components: {
    VueMarkdown,
    ModalLightbox,
  },
  data() {
    return {
      text: require(`../assets/markdown/${this.textSource}`).default,
    };
  },
  props: {
    heading: {
      required: false,
      type: String,
    },
    textSource: {
      required: true,
      type: String,
    },
  },
});
